'use client';
import React, { useState } from 'react';
import {   
  Calendar, Clock, Users, Home, BookOpen, DoorOpen,   
  Menu, X, School, DoorClosed, ChevronsLeft, ChevronsRight   
} from 'lucide-react';

const TimetableManagementDashboard = () => {
  const [activePage, setActivePage] = useState('dashboard');
  const [menuOpen, setMenuOpen] = useState(false);
  const [sidebarCollapsed, setSidebarCollapsed] = useState(false);

  const toggleMenu = () => setMenuOpen(!menuOpen);
  const toggleSidebar = () => setSidebarCollapsed(!sidebarCollapsed);

  // Navigation items
  const navigationItems = [
    { page: 'dashboard', title: 'Dashboard', icon: Home },
    { page: 'calendar', title: 'Calendar', icon: Calendar },
    { page: 'classes', title: 'Classes', icon: BookOpen },
    { page: 'teachers', title: 'Teachers', icon: Users },
    { page: 'rooms', title: 'Rooms', icon: DoorOpen }
  ];

  // Render page content based on the active page
  const renderPageContent = () => {
    switch (activePage) {
      case 'dashboard':
        return <div>Dashboard Content</div>;
      case 'calendar':
        return <div>Calendar View</div>;
      case 'classes':
        return <div>Classes Management</div>;
      case 'teachers':
        return <div>Teachers Management</div>;
      case 'rooms':
        return <div>Rooms Management</div>;
      default:
        return <div>Welcome to the Timetable System</div>;
    }
  };

  return (
    <div className="flex min-h-screen bg-gray-100">
      {/* Fixed Header */}
      <div className="fixed top-0 left-0 w-full bg-blue-900 text-white text-center text-2xl font-bold py-4 z-50">
        Timetable System
      </div>

      {/* Sidebar for Large Screens */}
      <aside
        className={`hidden lg:block bg-indigo-800 text-white fixed left-0 top-16 bottom-0 overflow-y-auto transition-all duration-300 ease-in-out z-40 ${
          sidebarCollapsed ? 'w-16' : 'w-64'
        }`}
      >
        {/* Sidebar Collapse Button */}
        <button
          onClick={toggleSidebar}
          className="absolute top-4 right-4 z-50 text-white hover:bg-indigo-700 rounded-full p-2"
        >
          {sidebarCollapsed ? <ChevronsRight className="h-5 w-5" /> : <ChevronsLeft className="h-5 w-5" />}
        </button>

        {/* Navigation */}
        <nav className="py-4">
          <ul className="space-y-2">
            {navigationItems.map(({ page, title, icon: Icon }) => (
              <li key={page}>
                <button
                  onClick={() => setActivePage(page)}
                  className={`w-full px-6 py-3 text-left flex items-center space-x-3 transition-colors ${
                    activePage === page ? 'bg-white text-indigo-800' : 'hover:bg-indigo-700'
                  }`}
                >
                  <Icon className="h-5 w-5" />
                  {!sidebarCollapsed && <span>{title}</span>}
                </button>
              </li>
            ))}
          </ul>
        </nav>
      </aside>

      {/* Mobile Navigation */}
      <div
        className={`fixed top-16 left-0 w-64 bg-indigo-800 text-white z-50 transform ${
          menuOpen ? 'translate-x-0' : '-translate-x-full'
        } transition-transform duration-300 ease-in-out lg:hidden h-[calc(100%-4rem)] overflow-y-auto`}
      >
        <button onClick={toggleMenu} className="absolute top-4 right-4">
          <X className="h-6 w-6" />
        </button>
        <nav className="py-4">
          <ul className="space-y-2">
            {navigationItems.map(({ page, title, icon: Icon }) => (
              <li key={page}>
                <button
                  onClick={() => {
                    setActivePage(page);
                    setMenuOpen(false);
                  }}
                  className={`w-full px-6 py-3 text-left flex items-center space-x-3 transition-colors ${
                    activePage === page ? 'bg-white text-indigo-800' : 'hover:bg-indigo-700'
                  }`}
                >
                  <Icon className="h-5 w-5" />
                  <span>{title}</span>
                </button>
              </li>
            ))}
          </ul>
        </nav>
      </div>

      {/* Top Mobile Header */}
      <header className="lg:hidden fixed top-0 left-0 w-full bg-blue-900 text-white flex justify-between items-center p-4 z-40">
        <button onClick={toggleMenu}>
          {menuOpen ? <X className="h-6 w-6" /> : <Menu className="h-6 w-6" />}
        </button>
        <h1 className="text-xl font-bold">Timetable System</h1>
      </header>

      {/* Main Content Area */}
      <main
        className={`flex-1 transition-all duration-300 ease-in-out lg:ml-64 mt-16 p-6 ${
          sidebarCollapsed ? 'lg:ml-16' : 'lg:ml-64'
        }`}
      >
        {renderPageContent()}
      </main>
    </div>
  );
};

export default TimetableManagementDashboard;
